import test from "ava"
import http from "ava-http"

const MongoClient = require("mongodb").MongoClient;

const mongoUrl = "mongodb://localhost:27017/";
const mongoDBName = "incident";
const mongoCol_locality = "dbLocality";
const mongoCol_incidents = "dbIncident";





test("/GET incidents", t => {

    return http.get("http://localhost:8000/incidents")
        .then((resp) => {
            t.true(typeof resp === typeof [], "The response is not an object")
        })
});


test("/POST incidents", async t => {
    const resp = await http.post("http://localhost:8000/incidents")

    t.true(resp === false, "Response must be false since no parameter was sent");

    // t.true(resp === 200);
    // t.true(resp === false, "The response should've been \"false\" because no parameters were sent");
})

